.App {
  text-align: center;

  > .title {
    font-size: 40px;
  }

  > .data-tables {
    display:flex;

    > .a-table {
      flex: 4;
      height: 400px;
      overflow: scroll;
    }

    > .spacer {
      flex: 1;
    }
  }

  > .data-map {
    display: flex;

    > iframe {
      flex: 10;
      height: 400px;
    }

    > .spacer {
      flex: 1;
    }
  }

  > .data-charts {
    display: flex;

    > .chart {
      flex: 4
    }

    > .spacer {
      flex: 1
    }
  }
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
